let calcButtons = document.querySelectorAll(".calc-btn")

calcButtons.forEach(btn => {
    btn.onmouseenter = function () {
        btn.classList.add("_hover-state")
        setTimeout(()=>{
            btn.classList.add("_hover-state2")
        },300)
    }
    btn.onmouseleave = function () {
        btn.classList.remove("_hover-state")
        btn.classList.remove("_hover-state2")
    }
})


